function sliderContests() {
    var swiper = new Swiper('.contests .swiper', {
        slidesPerView: '3',
        spaceBetween: 24,
        loop: true,
        pagination: {
            el: ".contests .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 1.8,
            },
            993: {
                slidesPerView: 3,
            },
        }
    })
}

function sliderVacancies() {
    var swiper = new Swiper('.vacancies .swiper', {
        slidesPerView: '3',
        spaceBetween: 24,
        navigation: {
            nextEl: ".vacancies .swiper-button-next",
            prevEl: ".vacancies .swiper-button-prev",
        },
        pagination: {
            el: ".vacancies .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 2,
            },
            993: {
                slidesPerView: 3,
            },
        }
    })
}


$(document).ready(function() {

    sliderContests();
    sliderVacancies();

    //Burger
    $('.burger').click(function () {
        $('.header__right').toggleClass('header__right--active')
    })

    $('.close').click(function () {
        $('.header__right').removeClass('header__right--active')
    })

    let burger = $('.burger')
    let headerRight = $('.header__right')

    $(document).click(function (e) {
        if ( !burger.is(e.target) && burger.has(e.target).length === 0 &&
            !headerRight.is(e.target) && headerRight.has(e.target).length === 0

        ) {
            headerRight.removeClass('header__right--active')
        }
    })

    // Hover-Click menu
    $(".submenu").hover(onIn, onOut);
    $(".submenu .submenu-show").click(function() {
        if ($(this).parents(".submenu").hasClass("submenu--active")) {
            $(this).parents(".submenu").removeClass("submenu--active")
        } else {
            $(this).parents(".submenu").addClass("submenu--active")
        }
    })

    //Input
    $(document).on('input', function () {

        if ($('.search-form__input').val()) {
            $('#placeholderDiv').hide();
        } else {
            $('#placeholderDiv').show();
        }

    });

    //Accordion
    $(".item-show").click(function () {
        let parent = $(this).parents(".item")
        if($(parent).hasClass("item--active")){
            $(".item").removeClass("item--active")
            $(".item-hidden").slideUp()
        }else{
            $(".item").removeClass("item--active")
            $(parent).addClass("item--active")
            $(".item-hidden").slideUp()
            $(this).siblings(".item-hidden").slideDown()
        }
    })

    //Input-file
    $('#file').on('change', function (e) {
        $(this).prev('span').html(e.target.files[0].name);
    });

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tabs-content").removeClass("tabs-content--active")
        $(this).parents(".tabs").find(`.tabs-content[data-content-path="${path}"]`).addClass("tabs-content--active")
    })

    // Select
    $(".custom_select select").niceSelect()

    // Editing
    $('.resume-card__btn').click(function () {
        $(this).parents('.resume').addClass('resume--active')
        $('.resume-editing').addClass('resume-editing--active')
    })

    $('.resume-editing__btn').click(function () {
        $('.resume').removeClass('resume--active')
        $('.resume-editing').removeClass('resume-editing--active')
    })

    // Delete
    $('.delete').click(function () {
        $(this).parents('.date-input').remove()
    })

    // Date
    new AirDatepicker('#date-input');
    new AirDatepicker('#date-input2');
    new AirDatepicker('#date-input3');
    new AirDatepicker('#date-input4');

    new AirDatepicker('#date-range', {
        range: true,
        multipleDatesSeparator: ' - '
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }


})

function onIn() {
    if (window.innerWidth > 992) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("submenu--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 992) {
        $(this).removeClass("submenu--active")
    }
}